
const logErrors = (method, message, LogMessage, statusCode) => {
    let errorData = {
        status: true,
        method: !method ? '' : method,
        message: !message ? 'Error! ' : message,
        LogMessage: !LogMessage ? 'An error occurred' : LogMessage,
        statusCode: !statusCode ? 404 : statusCode,
    };
    // errorData.status === true ? console.log(errorData) : '';
};

export default logErrors;
