import axios from 'axios';
import store from "../store";
// import router from './router';

var userToken = localStorage.algebraToken || null
userToken = JSON.parse(userToken)

var instance = axios.create({
    baseURL: store.state.rawURI,
    timeout: 80000,
    headers: { "Authorization": ("Bearer " + (userToken !== null ? userToken[0].token : null)), "Content-Type": "application/json; charset=utf-8;text/html" },
});

