const loader = (context, ref, options) => {
    let loader = context.$loading.show({
        container: ref,
        loader: (options ? options.loaderType : "spinner"),
        // backgroundColor: '#000',
        width: 30,
        height: 30,
        opacity: ( options && typeof options.opacity !== 'undefined' ? options.opacity : 0.7),
        color: (options && typeof options.color !== 'undefined' ? options.color : "#38a9ff"),
    });
    return loader;
};

export default loader;