import Vue from 'vue'
import Vuex from 'vuex'
import user from './User'

Vue.use(Vuex);


export default new Vuex.Store({
    modules: {
        user,
    },
    state: {
        // user: {
        //     fullname: "", username: "", email: "", avatar: "", id: "", user_type: "",
        // },
        appName: 'Algebra',
        staticImage: "http://medallion.preticash.com/storage/uploads/avatar/avatar.jpg/",
        rawURI: "http://medallion.preticash.com/api/",
        purchaseItems: {},
        productsForm: {},
        documentForm: {},
        tempStore: '',
    },
    mutations: {
        SET_TEMP_STORE(state, payload) {
            state.tempStore = payload;
        },
        SET_PURCHASE_ITEMS(state, payload) {
            state.purchaseItems = payload;
        },
        GET_PRODUCTS_FORM(state, payload) {
            state.productsForm = payload;
        },
        GET_DOCUMENT_FORM(state, payload) {
            state.documentForm = payload;
        },
    },
    actions: {
        SET_ITEMS_PURCHASE({commit}, payload) {
            commit('SET_PURCHASE_ITEMS', payload);
        },
    },
})
