import Vue from 'vue';
import Router from 'vue-router';
import MainLayout from "../layouts/Home/Index";
import SecondaryLayout from "../layouts/Home/HomeHeader2";
import LoginLayout from "../layouts/Auth/index";
import DashboardLayout from "../layouts/Dashboard/index";


const ResponseMessage = () => import("@/components/Notifications/ResponseMessage");
const SpinnerLoader = () => import("@/components/Loaders/Spinners");

Vue.use(Router);
Vue.component("response-message", ResponseMessage);
Vue.component("spinner", SpinnerLoader);

const router = new Router({
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            component: MainLayout,
            children: [
                {
                    path: '/',
                    name: "Home",
                    component: () =>import('../views/Home/index'),
                },
            ],
        },
        {
            path: '/dashboard',
            component: DashboardLayout,
            children: [
                {
                    path: '/',
                    name: 'Dashboard',
                    component: () => import('../views/Dashboard/index'),
                },
            ],
        },
        {
            path: '/d',
            redirect: '/',
            component: SecondaryLayout,
            children: [
                {
                    path: '/services',
                    name: 'Service',
                    meta: {
                        title: 'Services ',
                    },
                    component: () => import('../views/Services/index'),
                },
                {
                    path: '/licence/:id',
                    props: true,
                    name: "Licence",
                    meta: {
                        title: 'Licence',
                    },
                    component: () => import('../views/Licence'),
                },
                {
                    path: '/licenceDocument',
                    name: 'LicenceDocument',
                    meta: {
                        title: 'Licence Document',
                    },
                    component: () => import('../views/Licence/licenceDocument'),
                },
            ],
        },
        {
            path: '/auth',
            redirect: '/auth/login',
            component: LoginLayout,
            children: [
                {
                    path: 'login',
                    name: 'Login',
                    meta: {
                        title: 'Login',
                    },
                    component: () => import('../views/Auth/login'),
                },
                {
                    path: 'register',
                    name: 'Register',
                    meta: {
                        title: 'Register',
                    },
                    component: () => import('../views/Auth/register'),
                },

            ],
        },
        {
            path: '/dashboard',
            redirect: 'dashboard/',
            component: DashboardLayout,
        },
        // 404
        {
            path: '/404',
            meta: {
                title: '404',
            },
            component: () => import('../views/404/index'),
        },
        // Redirect to 404
        {
            path: '*', redirect: '/404', hidden: true,
        },
    ],
});

export default router;
