<template>
    <div>
        <section class="modals">
            <div class="container">
                <div class="modal fade" id="notification" role="dialog" tabindex="-1" aria-hidden="true">
                    <div class="modal-dialog">
                        <div class="modal-content unread-notification">
                            <div class="modal-body br" style="overflow: auto;max-height: 400px">
                                <div class="chatting-page">
                                    <div class="chat-freelancers">
                                        <div class="users">
                                            <ul v-for="(notification, index) in notifications" :key="index">
                                                <li>
                                                    <div class="user-img">
                                                        <img src="img/chatting/user1.jpg" alt="">
                                                        <span class="online-icon"><i class="icofont icofont-ui-press"></i></span>
                                                    </div>
                                                    <div class="notification-details">
                                                        <p class="notification-text"><b>{{notification.type}}</b> <br>
                                                            {{ notification.message }}
                                                        </p>

                                                        <p class="notification-time">About {{ timeAgoFunction(notification.created_at - 60 * 1000)}}</p>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "NotificationsModal",
        props: {
            notifications: Array,
        },
        mounted() {
            // console.log('notifications',this.$props.notifications)
        },
        methods: {
            timeAgoFunction(timestamp) {
                return this.$timeAgo.format(new Date(timestamp * 1000));
            },
        },
    }
</script>

