import Vue from 'vue'
import Vuex from 'vuex'
import ls from '../../functions/localstorage';

Vue.use(Vuex)

export default {
    state: {
        user: {},
    },
    mutations: {
        UPDATE_USER(state, { user }) {
            state.user = user
        },
        LOGOUT_USER(state) {
            state.user = {};
            ls.clearAll();
            // ls.clearStorage("algebraData");
            // ls.clearStorage("algebraToken");
            // ls.clearStorage("localCartItem");
            // ls.clearStorage("algebra_from_url");
            // ls.clearStorage("transaction_data");
            // ls.clearStorage("documentName");
            window.location='/';
        },
    },
    actions: {},
    getters: {
        user: state => state.user,
    },
}
