import Vue from 'vue'
import App from './App.vue'
import router from './functions/router'
import VueAxios from 'vue-axios'
import store from "./store/index"
import Server from './functions/$server'
import VuePageTitle from 'vue-page-title'
import encrypto from "./functions/encrypto";
import Spinners from "./components/Loaders/Spinners";
import NProgress from 'vue-nprogress'

Vue.use(VueAxios,Server);
Vue.use(Loading);

Vue.use(VuePageTitle, {
  // prefix: 'My App - ',
  suffix: '- ' + store.state.appName,
});

const nprogress = new NProgress({ parent: 'body' })

// custom functions
import Loading from "vue-loading-overlay"
import 'vue-loading-overlay/dist/vue-loading.css'
import localstorage from './functions/localstorage'
import loader from "./functions/loader";
import validate from "./functions/validate";
import getAvatar from "./functions/getAvatar";
import logErrors from "./functions/logErrors";
import TimeAgo from 'javascript-time-ago'
import getDateYear from "./functions/getDateYear";

Vue.config.productionTip = false;
window.eventBus = new Vue();

import en from 'javascript-time-ago/locale/en'
TimeAgo.addLocale(en);
// Create relative date/time formatter.
const timeAgo = new TimeAgo('en-US');

Vue.prototype.$ls = localstorage;
Vue.prototype.$loader = loader;
Vue.prototype.$validate = validate;
Vue.prototype.$avatar = getAvatar;
Vue.prototype.$encrypto = encrypto;
Vue.prototype.$spinners = Spinners;
Vue.prototype.$errors = logErrors;
Vue.prototype.$timeAgo = timeAgo;
Vue.prototype.$dateYear = getDateYear;

new Vue({
  router,
  store,
  nprogress,
  render: h => h(App),
}).$mount('#app');
