<template>
  <div id="app">
    <div v-if="welcome === true" class="welcome-message slideInRight">
      <div class="message-box animated">
        <i class="fa fa-check-circle"></i>
        <h1>Logged In</h1>
        <p>You have successfully logged in to {{ $store.state.appName }}</p>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import store from "@/store";
export default {
  name: 'app',
  data() {
    return {
      welcome: false,
    }
  },
  mounted() {
    this.generateUserData();
    window.eventBus.$on("loggedIn", val => {
      if (val === true) {
        console.log(val)
        this.welcome === true;
        // $(".message-box").removeClass("slideOutRight");
        // $(".message-box").addClass("slideInRight");
        // setTimeout(() => {
        //   $(".message-box").removeClass("slideInRight");
        //   $(".message-box").addClass("slideOutRight");
        // }, 5000);
        // setTimeout(() => {
        //   this.welcome = false;
        // }, 6000);
      }
    })
  },
  watch: {
    $route(to,from) {
      let fromUrl = {
        name: from.name,
        fullPath: from.fullPath,
        meta: from.meta,
        hash: from.hash,
        params: from.params,
        path: from.path,
        query: from.query,
      };
      if (this.$ls.clearStorage("algebra_from_url")) {
        this.$ls.setStorage("algebra_from_url", fromUrl);
      }
    },
  },
  methods: {
    generateUserData() {
      if (this.$ls.checkStorage("algebraData")) {
        var user = this.$ls.getStorage("algebraData");
        user = user[0];
        store.commit('UPDATE_USER', { user });
      }
    },
  },
}
</script>

