<template>
    <div>
        <header>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-lg-8 col-sm-12 col-md-8 primary-menu order-sm-1 order-2">
                        <nav class="navbar navbar-expand-lg">
                            <div class="navbar-brand">
                                <router-link to="/" style="color: #000" class="logo js-scroll-trigger">
                                    <img src="/img/tbt2.png" alt="">
                                    <h1>lgebra</h1>
                                </router-link>
                                <router-link to="/" style="color: #000;" class="s-logo js-scroll-trigger">
                                    <img src="/img/tbt2.png" alt="">
                                    <h1>lgebra</h1>
                                </router-link>
                            </div>
                            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                                <span class="navbar-toggler-icon"></span>
                            </button>

                            <navigation_left></navigation_left>
                        </nav>
                    </div>
                    <div class="col-lg-4 col-md-4 col-sm-12 header-notification order-sm-2 order-1">
                        <navigation_right :notificationsTotal="notifications.total"></navigation_right>
                    </div>
                </div>
            </div>
        </header>
        <notifications-modal :notifications="notifications.data"></notifications-modal>
        <router-view></router-view>
        <section id="followers">
            <div class="container">
            </div>
        </section>
        <home-footer></home-footer>
    </div>
</template>

<script>
    import HomeFooter from "../Home/HomeFooter";
    import navigation_left from "../Home/navigation_left";
    import navigation_right from "../Home/navigation_right";
    import NotificationsModal from "../../components/Modals/NotificationsModal";
    export default {
        name: "index",
        data() {
            return {
                notifications: {
                    data: [],
                    first_page_url: '',
                    next_page_url: '',
                    last_page_url: '',
                    last_page: '',
                    prev_page_url: '',
                    total: '',
                },
            }
        },
        components: {HomeFooter, navigation_right, navigation_left, NotificationsModal},
        mounted() {
            this.getNotifications()
        },
        methods: {
            getNotifications() {
                this.axios.get('/notifications/all/100')
                    .then(res => {
                        const notifications = res.data.data.notifications;
                        // console.log(notifications)
                        this.notifications.first_page_url = notifications.first_page_url;
                        this.notifications.last_page_url = notifications.last_page_url;
                        this.notifications.last_page = notifications.last_page;
                        this.notifications.next_page_url = notifications.next_page_url;
                        this.notifications.prev_page_url = notifications.prev_page_url;
                        this.notifications.total = parseInt(notifications.total, 10);
                        this.notifications.data = notifications.data;
                        // this.$errors('Success!', res.data.data, res.data.message, res.status)
                    })
                    .catch(err => {
                        this.$errors('Error!', err.response.data.message, err.message,err.response.status)
                    })
            },
        },
    }
</script>

<style scoped>
    .s-logo img {
        float: left;
    }
    .logo img {
        float: left;
    }
    .s-logo h1 {
        float: left;
        margin: 0 -2px;
        position: relative;
        top: 1px;
        font-size: 30px;
        font-weight: bold;
        color: #fff;
        /*text-shadow: 0 1px 2px rgba(0,0,0,.3);*/
    }
    .logo h1 {
        float: left;
        margin: 0 -2px;
        position: relative;
        top: 4px;
        font-size: 50px;
        font-weight: bold;
        color: #F58634;
        /*text-shadow: 0 1px 2px rgba(0,0,0,.3);*/
    }
</style>
