<template>
    <div>
        <nav class="navbar navbar-expand-lg">
            <ul class="navbar-nav ml-auto right-menu" v-if="signedIn === false ">
                <li class="nav-item">
                    <div class="form-group select-work-type">
                        <router-link @click="storeLastUrl()" :to="{ name: 'Register' }"><button class="auth-btn">Signup</button></router-link>
                        <router-link @click="storeLastUrl()" :to="{ name: 'Login' }"><button class="auth-btn">Login</button></router-link>
                    </div>
                </li>
            </ul>
            <ul class="navbar-nav ml-auto right-menu" v-else>
                <!--                                <li class="nav-item">-->
                <!--                                    <a class="nav-link bell" href="#" data-toggle="modal" data-target="#notification"><i class="fa fa-shopping-cart"></i>-->
                <!--                                        <span>0</span>-->
                <!--                                    </a>-->
                <!--                                </li>-->
                <li class="nav-item">
                    <a class="nav-link message" href="#" data-toggle="modal" data-target="#notification"><i class="far fa-bell"></i>
                        <span style="position:relative;right: 8px">{{notificationsTotal ? notificationsTotal : 0 }}</span>
                    </a>
                </li>
                <li class="nav-item dropdown menu-padding dropdown-user">
                    <a class="nav-link user-img" href="#" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><img width="40" :src="avatar" :alt="getUserDetails.user.username">
                        <span class="online"></span>
                    </a>
                    <ul class="dropdown-menu">
                        <li class="nav-item"><router-link to="/dashboard" class="dropdown-item">Dashboard</router-link></li>
                        <li class="nav-item"><a href="#" class="dropdown-item">Profile</a></li>
                        <li class="nav-item"><a @click="logout()" class="dropdown-item">Logout</a></li>
                    </ul>
                </li>
            </ul>

        </nav>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    export default {
        name: "navigation_right",
        data() {
            return {
                signedIn: false,
                userData: {},
                avatar: "",
            }
        },
        props: ['notificationsTotal'],
        computed: mapState({
            getUserDetails: state => state.user,
            getAvatar: state => state.user.avatar,
        }),
        mounted() {
            this.checkUserData();
        },
        methods: {
            storeLastUrl() {
                let route = this.$route.path;
                console.log(route);
                // if (this.$ls.clearStorage("algebra_last_url")) {
                //     this.$ls.setStorage("algebra_last_url",to.path);
                // }
            },
            logout() {
                this.$store.commit('LOGOUT_USER')
            },
            checkUserData() {
                var userData = this.getUserDetails;
                if (userData.user.username) {
                    this.signedIn = true;
                    this.userData = userData.user;
                    let avt = this.$avatar({state:{ av: this.getAvatar, us: this.getUserDetails.user.username }});
                    this.avatar = avt;
                }else {
                    this.signedIn = false;
                    this.userData = {};
                }
            },
        },
    }
</script>

<style scoped>
    .auth-btn {
        padding: 6px 20px;
        color: #fff;
        margin: 0 10px 7px 10px;
        border-radius: 25px;
        background-color: transparent;
        border:solid 1px #fff;
    }
</style>